<!--  -->
<template>
  <div class="contentbox">
    <div class="bg flexbox">
      <div class="datalist_box">
        <div class="datalist">
          <alldata @returnData="getData"></alldata>
        </div>
      </div>
      <div class="datamap_box">
        <div class="top_select">
          <div class="select_box">
            <div class="select_first">
              <div class="select_check" @click.prevent>
                <input
                  class="inp-cbx selected"
                  id="select_river"
                  type="checkbox"
                  style="display: none;"
                  :checked="select_river"
                />
                <label class="cbx" for="select_river">
                  <span>
                    <svg width="12px" height="10px">
                      <use xlink:href="#check" />
                    </svg>
                  </span>
                  <span>筛选流域</span>
                </label>
              </div>
              <el-select v-model="selectRiver" filterable placeholder="请选择流域" class="selecter">
                <el-option
                  v-for="item in river_options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="select_second">
              <div class="select_check" @click.prevent>
                <input
                  class="inp-cbx selected"
                  id="select_type"
                  type="checkbox"
                  style="display: none;"
                  :checked="select_type"
                />
                <label class="cbx" for="select_type">
                  <span>
                    <svg width="12px" height="10px">
                      <use xlink:href="#check" />
                    </svg>
                  </span>
                  <span>筛选数据</span>
                </label>
              </div>
              <el-select v-model="selectType" filterable placeholder="请选择数据类型" class="selecter">
                <el-option
                  v-for="item in typeOptios"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="select_third">
              <div class="select_check" @click.prevent="select_boat=!select_boat">
                <input
                  class="inp-cbx"
                  id="select_boat"
                  type="checkbox"
                  style="display: none;"
                  :checked="select_boat"
                />
                <label class="cbx" for="select_boat">
                  <span>
                    <svg width="12px" height="10px">
                      <use xlink:href="#check" />
                    </svg>
                  </span>
                  <span>筛选小船</span>
                </label>
              </div>
              <el-select v-model="selectboatId" filterable placeholder="请选择小船" class="selecter">
                <el-option
                  v-for="item in boat_options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="select_forth">
              <div class="select_check" @click.prevent="select_date=!select_date">
                <input
                  class="inp-cbx"
                  id="select_date"
                  type="checkbox"
                  style="display: none;"
                  :checked="select_date"
                />
                <label class="cbx" for="select_date">
                  <span>
                    <svg width="12px" height="10px">
                      <use xlink:href="#check" />
                    </svg>
                  </span>
                  <span>筛选日期</span>
                </label>
              </div>
              <div class="selecter c-datepicker-date-editor J-datepicker-range">
                <i class="c-datepicker-range__icon kxiconfont icon-clock"></i>
                <input placeholder="请选择开始日期" name class="c-datepicker-data-input" value />
                <span class="c-datepicker-range-separator">-</span>
                <input placeholder="请选择结束日期" name class="c-datepicker-data-input" value />
              </div>
            </div>
            <svg class="inline-svg">
              <symbol id="check" viewBox="0 0 12 10">
                <polyline points="1.5 6 4.5 9 10.5 1" />
              </symbol>
            </svg>
          </div>
        </div>
        <div class="bottom_map">
          <linechart
            ref="linechart"
            :boatData="boatData"
            :select_boat="select_boat"
            :select_date="select_date"
            :selectboatId="selectboatId"
            :selectRiver="selectRiver"
            :selectType="selectType"
            :selectdateStart="selectdateStart"
            :selectdateEnd="selectdateEnd"
            :echartsStart="echartsStart"
            :boat_options="boat_options"
            :father_redraw="can_redraw"
            @get_canredraw="get_canredraw"
          ></linechart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import alldata from "../../components/content/AllData";
import { dateplugin } from "../../js/content/moment.min.js";
import { datepicker } from "../../js/content/datepicker.all.min.js";
import linechart from "../../components/content/LineChart";
import { clearTimeout } from "timers";
export default {
  data() {
    return {
      can_redraw: false,
      boatData: [], //从服务器获取到的小船数据收集
      select_date: false, //check 标识符
      select_type: false, //同上
      select_boat: false, //同上
      select_river: false, //同上
      maxdate: "", //筛选日期的最大日期
      selectboatId: "", //选中的小船编号
      selectRiver: "", //选中的流域
      selectType: "", //选中的类型
      selectdateStart: "", //选中的起始时间
      selectdateEnd: "", //选中的终止时间
      echartsStart: false,
      typeOptios: [
        {
          label: "pH值",
          value: "pH"
        },
        {
          label: "氮氧值",
          value: "NH4"
        }
      ]
    };
  },

  components: { alldata, linechart },

  computed: {
    //筛选小船的select设置
    boat_options() {
      var boatarr = new Array();
      for (let i = 0; i < this.boatData.length; i++) {
        boatarr.push(this.boatData[i].boatId);
      }
      var distinct_boatId = this.distinct(boatarr);
      var boatId_options = new Array();
      for (let i = 0; i < distinct_boatId.length; i++) {
        var label = "小船" + distinct_boatId[i] + "号";
        var obj = {
          value: distinct_boatId[i],
          label: label
        };
        boatId_options.push(obj);
      }
      return boatId_options;
    },
    //筛选流域的select设置
    river_options() {
      var riverarr = new Array();
      for (let i = 0; i < this.boatData.length; i++) {
        riverarr.push(this.boatData[i].siteName);
      }
      var distinct_river = this.distinct(riverarr);
      var river_options = new Array();
      for (let i = 0; i < distinct_river.length; i++) {
        var obj = {
          value: distinct_river[i],
          label: distinct_river[i]
        };
        river_options.push(obj);
      }
      return river_options;
    }
  },
  created() {
    this.getNowFormatDate();
  },
  mounted() {
    dateplugin();
    datepicker();
    this.initDate_select();
    this.listenDate_select();
  },

  methods: {
    //数组去重
    distinct(arr) {
      return Array.from(new Set([...arr]));
    },
    test() {
      console.log(this.boat_options);
    },
    //从服务器获取小船数据
    getData(boatData) {
      this.boatData = boatData;
      this.selectRiver = this.boatData[0].siteName;
      this.selectType = "pH";
      this.echartsStart = true;
    },
    //获取当前时间作为筛选日期的最大时间
    getNowFormatDate() {
      //获取当前时间
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      var month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var strDate = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var strHour =
        date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var strMinutes =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var strSeconds =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      var currentdate =
        date.getFullYear() +
        seperator1 +
        month +
        seperator1 +
        strDate +
        " " +
        strHour +
        seperator2 +
        strMinutes +
        seperator2 +
        strSeconds;
      this.maxdate = currentdate;
    },
    //初始化筛选日期选择器
    initDate_select() {
      var _this = this;
      $(".J-datepicker-range").datePicker({
        hasShortcut: true,
        min: "2018-08-31 00:00:00",
        max: _this.maxdate,
        isRange: true,
        shortcutOptions: [
          {
            name: "昨天",
            day: "-1,-1",
            time: "00:00:00,23:59:59"
          },
          {
            name: "最近一周",
            day: "-7,0",
            time: "00:00:00,"
          },
          {
            name: "最近一个月",
            day: "-30,0",
            time: "00:00:00,"
          },
          {
            name: "最近三个月",
            day: "-90, 0",
            time: "00:00:00,"
          }
        ]
      });
    },
    //监听筛选日期是否选择
    listenDate_select() {
      var _this = this;
      $(".is-plain").bind("click", function() {
        setTimeout(() => {
          var start_time = $(".c-datepicker-data-input")
            .eq(0)
            .val();
          var end_time = $(".c-datepicker-data-input")
            .eq(1)
            .val();
          if (start_time != null && start_time != "") {
            _this.select_date = true;
            _this.selectdateStart = start_time;
            _this.selectdateEnd = end_time;
            //重新渲染折线图
          } else {
            _this.select_date = false;
          }
        }, 50);
      });
      $(".c-datepicker-picker__shortcut").click(function() {
        setTimeout(() => {
          var start_time = $(".c-datepicker-data-input")
            .eq(0)
            .val();
          var end_time = $(".c-datepicker-data-input")
            .eq(1)
            .val();
          if (start_time != null && start_time != "") {
            _this.select_date = true;
            _this.selectdateStart = start_time;
            _this.selectdateEnd = end_time;
            //重新渲染折线图
          } else {
            _this.select_date = false;
          }
        }, 50);
      });
    },
    //重新绘制折线图
    redraw() {
      this.$refs.linechart.initChart();
    },
    //返回是否可重绘折线图控制标识符
    get_canredraw(canredraw) {
      this.can_redraw = canredraw;
    },
    //设置默认时间
    defaultDate() {
      var start = this.maxdate.substr(0, 11);
      start += "00:00:00";
      $(".c-datepicker-data-input")
        .eq(0)
        .val(start);
      $(".c-datepicker-data-input")
        .eq(1)
        .val(this.maxdate);
    }
  },
  watch: {
    //监听折线图控制标识符
    can_redraw(val, oldVal) {
      if (val) {
        this.redraw();
      }
    },
    select_date(val, oldVal) {
      if (val) {
        var input = $(".c-datepicker-data-input")
          .eq(1)
          .val();
        if (input != null && input != "") {
          //重新绘制折线图
        } else {
          //设置默认时间
          this.defaultDate();
        }
      }
    },
    select_boat(val, oldVal) {
      if (val) {
        if (this.selectboatId != null && this.selectboatId != "") {
          //重新绘制折线图
        } else {
          //默认选择第一条小船
          this.selectboatId = this.boat_options[0].value;
        }
      } else {
      }
    },
    select_river(val, oldVal) {
      if (val) {
        if (this.selectRiver != null && this.selectRiver != "") {
          //重新绘制折线图
        } else {
          //弹窗提示
          alert("未选定流域！");
          this.select_river = false;
        }
      }
    },
    select_type(val, oldVal) {
      if (val) {
        if (this.selectType != null && this.selectType != "") {
          //重新绘制折线图
        } else {
          //弹窗提示
          alert("未选定流域！");
          this.select_type = false;
        }
      }
    },
    selectboatId(val, oldVal) {
      if (val != null && val != "") {
        this.select_boat = true;
        //重新绘制折线图
      }
    },
    selectRiver(val, oldVal) {
      if (val != null && val != "") {
        this.select_river = true;
        //重新绘制折线图
      }
    },
    selectType(val, oldVal) {
      if (val != null && val != "") {
        this.select_type = true;
        //重新绘制折线图
      }
    }
  }
};
</script>
