<!--  -->
<template>
  <div style="width:100%;display:flex;justify-content:center;">
    <div id="main" style="width: 90%;height:320px;" v-show="!errorflag"></div>
    <errorpage v-show="errorflag"></errorpage>
  </div>
</template>

<script>
import errorpage from "./Errorpage";
export default {
  data() {
    return {
      son_redraw: this.father_redraw,
      errorflag: false
    };
  },
  props: [
    "boatData",
    "select_boat",
    "select_date",
    "selectboatId",
    "selectRiver",
    "selectType",
    "selectdateStart",
    "selectdateEnd",
    "echartsStart",
    "boat_options",
    "father_redraw"
  ],
  components: {
    errorpage
  },

  computed: {
    titleText() {
      var typestr = "";
      if (this.selectType == "pH") {
        typestr = "pH值";
      } else if (this.selectType == "NH4") {
        typestr = "氮氧值";
      }
      var text = "";
      text = this.selectRiver + typestr;
      return text;
    },
    legend() {
      if (this.select_boat) {
        var legendarr = new Array();
        var legend = "小船" + this.selectboatId + "号";
        legendarr.push(legend);
        return legendarr;
      } else {
        var legendarr = new Array();
        for (let i = 0; i < this.boat_options.length; i++) {
          legendarr.push(this.boat_options[i].label);
        }
        return legendarr;
      }
    },
    river_selected() {
      var riverSelected = new Array();
      for (let i = 0; i < this.boatData.length; i++) {
        if (this.boatData[i].siteName == this.selectRiver) {
          riverSelected.push(this.boatData[i]);
        }
      }
      return riverSelected;
    },
    boat_selected() {
      if (this.select_boat) {
        return this.selectByBoatId(this.river_selected, this.selectboatId);
      } else {
        return this.river_selected;
      }
    },
    date_selected() {
      if (this.select_date) {
        var dateSelected = new Array();
        for (let i = 0; i < this.river_selected.length; i++) {
          if (
            this.selectdateStart < this.river_selected[i].dateTime &&
            this.river_selected[i].dateTime < this.selectdateEnd
          ) {
            dateSelected.push(this.river_selected[i]);
          }
        }
        return dateSelected;
      } else {
        return this.river_selected;
      }
    },
    all_selected() {
      if (this.select_boat && this.select_date) {
        var all_selected = new Array();
        for (let i = 0; i < this.boat_selected.length; i++) {
          if (
            this.selectdateStart < this.boat_selected[i].dateTime &&
            this.boat_selected[i].dateTime < this.selectdateEnd
          ) {
            all_selected.push(this.boat_selected[i]);
          }
        }
        return all_selected;
      } else if (this.select_boat == true && this.select_date == false) {
        return this.boat_selected;
      } else if (this.select_boat == false && this.select_date == true) {
        return this.date_selected;
      } else {
        return this.river_selected;
      }
    },
    xAxis_data() {
      var x_data = new Array();
      for (let i = 0; i < this.all_selected.length; i++) {
        x_data.push(this.all_selected[i].dateTime);
      }
      return x_data;
    },
    series_data() {
      var series_data = new Array();
      if (this.selectType == "pH" && this.select_boat) {
        var name = "小船" + this.selectboatId + "号";
        var type = "line";
        var data = new Array();
        for (let i = 0; i < this.all_selected.length; i++) {
          data.push(this.all_selected[i].pH);
        }
        var obj = {
          name: name,
          type: type,
          data: data
        };
        series_data.push(obj);
        return series_data;
      } else if (this.selectType == "pH" && !this.select_boat) {
        for (let i = 0; i < this.boat_options.length; i++) {
          var name = this.boat_options[i].label;
          var type = "line";
          var all_data = this.selectByBoatId(
            this.all_selected,
            this.boat_options[i].value
          );
          var data = new Array();
          for (let i = 0; i < all_data.length; i++) {
            data.push(all_data[i].pH);
          }
          var obj = {
            name: name,
            type: type,
            data: data
          };
          series_data.push(obj);
        }
        return series_data;
      }
    }
  },

  mounted() {},
  watch: {
    echartsStart(val, oldVal) {
      if (val) {
        this.initChart();
      }
    },
    series_data(val, oldVal) {
      var val_data = val.map(val => val.data);
      var data_check = new Array();
      for (let i = 0; i < val_data.length; i++) {
        data_check = data_check.concat(val_data[i]);
      }

      if (data_check.length == 0) {
        this.errorflag = true;
      } else {
        this.errorflag = false;
        this.son_redraw = true;
        this.$emit("get_canredraw", this.son_redraw);
      }
    }
  },
  methods: {
    initChart() {
      this.son_redraw = false;
      this.$emit("get_canredraw", this.son_redraw);
      var _this = this;
      var myChart = this.$echarts.init(document.getElementById("main"));
      // 使用刚指定的配置项和数据显示图表。
      myChart.clear();
      myChart.setOption({
        title: {
          text: _this.titleText
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: _this.legend
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          right: "30",
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: _this.xAxis_data,
          boundaryGap: ["20%", "0%"]
        },
        yAxis: {
          type: "value"
        },
        series: _this.series_data,
        dataZoom: [
          {
            type: "slider",
            showDetail:false,
            show: true, //flase直接隐藏图形
            xAxisIndex: [0],
            left: "6%", //滚动条靠左侧的百分比
            bottom: 0,
            start: 0, //滚动条的起始位置
            end: 30 //滚动条的截止位置（按比例分割你的柱状图x轴长度）
          }
        ]
      });
    },
    selectByBoatId(data, id) {
      var result = new Array();
      for (let i = 0; i < data.length; i++) {
        if (data[i].boatId == id) {
          result.push(data[i]);
        }
      }
      return result;
    }
  }
};
</script>
