<script>
import { start } from "../../../public/static/jquery.bootstrap.newsbox.min.js";
import { clearTimeout } from "timers";
export default {
  data() {
    return {
      Newsbox: {}
    };
  },

  components: {},

  computed: {
    data() {
      return this.$store.state.allData;
    }
  },

  created() {
    this.fromSessionStorage();
    this.getData();
    
  },

  mounted() {
    start();
    this.$nextTick(() => {
      this.initTable();
    });
  },

  methods: {
    // 重新初始化滚动插件即开始列表滚动
    restart() {
      this.initTable();
      console.log("重新开始");
    },
    // 停止插件的计时器即停止列表滚动
    stop() {
      window.clearTimeout(this.Newsbox.timer);
      console.log("停止了");
      // console.log(this.Newsbox.timer);
    },
    // 实现定时更新数据
    update() {
      var _this = this;
      setInterval(() => {
        _this.$axios.get("api/data").then(data => {
          // console.log(data.data);
          // console.log("更新前:");
          // console.log(_this.data);
          // 暂停功能：关闭计时器
          // if(this.Newsbox.timer){
          //   _this.stop();
          // }
          // console.log("length:" + data.data.length);

          for (let index = 0; index < data.data.length; index++) {
            // _this.$Vue.set(_this.data, index, data.data[index]);
            // console.log(data.data[index]);
            $(".siteName")
              .eq(index)
              .text(data.data[data.data.length - index - 1].siteName);
            $(".dateTime")
              .eq(index)
              .text(data.data[data.data.length - index - 1].dateTime);
            $(".pH")
              .eq(index)
              .text(data.data[data.data.length - index - 1].pH);
            $(".DO")
              .eq(index)
              .text(data.data[data.data.length - index - 1].DO);
            $(".NH4")
              .eq(index)
              .text(data.data[data.data.length - index - 1].NH4);
            $(".CODMn")
              .eq(index)
              .text(data.data[data.data.length - index - 1].CODMn);
            $(".TOC")
              .eq(index)
              .text(data.data[data.data.length - index - 1].TOC);
            $(".attribute")
              .eq(index)
              .text(data.data[data.data.length - index - 1].attribute);
            $(".status")
              .eq(index)
              .text(data.data[data.data.length - index - 1].status);
            // console.log("修改完毕:" + index);
            // 当数据更新完后，重新启动功能
            // if (index == data.data.length - 1) {
            //   _this.restart();
            // }
          }
        });
      }, 20000);
    },
    // 插入新记录测试
    insert() {
      var newData = {
        id: 11,
        siteName: "宁夏中卫新墩11",
        dateTime: "2019-06-22 08:00",
        pH: "7.67",
        DO: "8.56",
        NH4: "0.13",
        CODMn: "5.3",
        TOC: "--",
        level: "III",
        attribute: "甘-宁省界",
        status: "正常"
      };
      this.$axios
        .post("/data", newData)
        .then(data => {
          // console.log("加入前:");
          // console.log(this.data);
          this.$Vue.set(this.data, this.data.length, data.data);
          // console.log("加入后:");
          // console.log(this.data);
        })
        .then(data => {
          // this.initTable();
        });
    },
    // 初次从后端获取数据
    getData() {
      this.$emit("returnData", this.data);
      // this.$axios
      //   .get("/api/getAllData")
      //   .then(data => {
      //     // for (let index = 0; index < data.data.length; index++) {
      //     //   // console.log(data.data[data.data.length-index-1]);

      //     //   this.data[index]=data.data[data.data.length-index-1];
      //     //   if (index==data.data.length-1) {
      //     //     start();
      //     //   }

      //     // }
      //     this.data = data.data.data;
      //     this.$emit("returnData",this.data);
      //     // console.log("当前data:");
      //     // console.log(this.data);
      //     // 引用插件
      //     start();
      //   })
      //   .then(data => {
      //     this.initTable();
      //   });
    },
    // 初始化滚动插件的方法
    initTable() {
      if (this.Newsbox.timer) {
        window.clearTimeout(this.Newsbox.timer);
      }
      this.Newsbox = $(".demo2").bootstrapNews({
        newsPerPage: 10,
        autoplay: true,
        pauseOnHover: true,
        direction: "up",
        newsTickerInterval: 1000
      });
    },
    fromSessionStorage() {
      if (sessionStorage.getItem("store")) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem("store"))
          )
        );
        sessionStorage.removeItem("store");
      }
    }
  }
};
</script>
